'use client';

import { useState } from 'react';
import DashboardLayout from '@/components/DashboardLayout';
import ProjectHeader from './_components/ProjectHeader';
import ProjectToolbar from './_components/ProjectToolbar';
import ProjectTable from './_components/ProjectTable';
import PageWrapper from '@/components/PageWrapper';

// Mock data to simulate backend response
const initialProjects = [
  {
    id: 1,
    name: 'Seaside Villa',
    status: 'Completed',
    progress: 100,
  },
  {
    id: 2,
    name: 'Metropolitan Tower',
    status: 'In Progress',
    progress: 75,
  },
  {
    id: 3,
    name: 'Urban Oasis Park',
    status: 'On Hold',
    progress: 30,
  },
  {
    id: 4,
    name: 'Phoenix Convention Center',
    status: 'Cancelled',
    progress: 10,
  },
];

export default function ProjectsPage() {
  const [projects, setProjects] = useState(initialProjects);

  const handleAddProject = () => {
    // This would typically open a modal or navigate to a new page
    console.log('Adding new project...');
    // For now, let's add a dummy project to show functionality
    setProjects([
      ...projects,
      {
        id: projects.length + 1,
        name: `New Project ${projects.length + 1}`,
        status: 'In Progress',
        progress: Math.floor(Math.random() * 100),
      },
    ]);
  };

  return (
    <DashboardLayout showHeader={false}>
      <PageWrapper>
        <div className="flex w-full flex-col gap-6">
          <ProjectHeader onAddProject={handleAddProject} />
          <section className="flex flex-col w-full gap-4">
            <ProjectToolbar />
            <ProjectTable projects={projects} />
          </section>
        </div>
      </PageWrapper>
    </DashboardLayout>
  );
}